"use client";

import React, { useState, useEffect, useRef } from "react";
import { useRouter } from "next/navigation";
import type { MenuItem, UserInfo } from "@/types/home.types";
import UserMenu from "../navbar/UserMenu";

interface NavbarProps {
  menuItems: MenuItem[];
  user: UserInfo;
}


export function Navbar({ menuItems, user }: NavbarProps) {
  const router = useRouter();

  const [desktopOpenIndex, setDesktopOpenIndex] = useState<number | null>(null);
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  const [mobileOpenIndex, setMobileOpenIndex] = useState<number | null>(null);

  const mobileMenuRef = useRef<HTMLDivElement>(null);

  // Cerrar menú mobile al hacer click fuera
  useEffect(() => {
    const handler = (e: MouseEvent) => {
      if (
        mobileMenuRef.current &&
        !mobileMenuRef.current.contains(e.target as Node)
      ) {
        setMobileMenuOpen(false);
        setMobileOpenIndex(null);
      }
    };
    document.addEventListener("mousedown", handler);
    return () => document.removeEventListener("mousedown", handler);
  }, []);

  function handleLogout() {
    localStorage.removeItem("token");
    sessionStorage.removeItem("token");
    router.replace("/");
  }

  return (
    <nav className="w-full bg-white shadow-sm fixed top-0 left-0 z-50 h-[72px]">
      <div className="max-w-[1300px] mx-auto px-6 h-full flex items-center justify-between">

        {/* LOGO */}
        <div className="flex items-center">
          <img
            src="/assets/logo_azul.png"
            alt="Logo"
            className="h-14 w-auto select-none cursor-pointer"
            onClick={() => router.push("/portal")}
          />
        </div>

        {/* DESKTOP MENU */}
        <div className="hidden md:flex items-center gap-6 h-full">

          {menuItems.map((item, index) => {
            const hasSubmenu = item.submenu && item.submenu.length > 0;
            const isOpen = desktopOpenIndex === index;

            return (
              <div
                key={index}
                className="relative h-full flex items-center"
                onMouseEnter={() => setDesktopOpenIndex(index)}
                onMouseLeave={() => setDesktopOpenIndex(null)}
              >
                {/* BUTTON o LINK */}
                {!hasSubmenu ? (
                  <a
                    href={item.href}
                    className={`px-4 py-2 rounded-md text-sm font-semibold transition
                      ${item.active ? "text-[#350A63]" : "text-[#2D1540] hover:text-[#350A63]"}
                    `}
                  >
                    {item.label}
                  </a>
                ) : (
                  <button
                    className={`px-4 py-2 flex items-center gap-1 font-semibold text-sm transition
                      ${item.active ? "text-[#350A63]" : "text-[#2D1540] hover:text-[#350A63]"}
                    `}
                  >
                    {item.label}
                    <span className="text-xs mt-[2px]">
                      ▼
                    </span>
                  </button>
                )}

                {/* DESKTOP DROPDOWN */}
                {hasSubmenu && isOpen && (
                  <div className="absolute left-0 top-full mt-1 bg-white w-56 shadow-xl rounded-lg py-3 z-50">
                    {item.submenu!.map((sub, i) => (
                      <a
                        key={i}
                        href={sub.href}
                        className="block px-4 py-2 text-sm text-[#2D1540] hover:bg-gray-100"
                      >
                        {sub.label}
                      </a>
                    ))}
                  </div>
                )}
              </div>
            );
          })}
        </div>

        {/* USER MENU DESKTOP */}
        <div className="hidden md:flex items-center gap-3">
          <img
            src={user.avatar || "/assets/user.png"}
            alt="user"
            className="h-10 w-10 rounded-full border object-cover cursor-pointer"
          />

          <button
            onClick={handleLogout}
            className="text-sm text-red-600 hover:underline"
          >
            Logout
          </button>
        </div>

        {/* HAMBURGER */}
        <button
          className="md:hidden flex flex-col gap-1"
          onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
        >
          <span className="h-[3px] w-7 bg-black"></span>
          <span className="h-[3px] w-7 bg-black"></span>
          <span className="h-[3px] w-7 bg-black"></span>
        </button>
      </div>

      {/* MOBILE MENU */}
      <div
        ref={mobileMenuRef}
        className={`
          md:hidden overflow-hidden transition-all duration-300 bg-white
          ${mobileMenuOpen ? "max-h-[600px] opacity-100" : "max-h-0 opacity-0"}
        `}
      >
        <div className="py-4 px-6 space-y-2">
          {menuItems.map((item, index) => {
            const hasSubmenu = item.submenu && item.submenu.length > 0;
            const isOpen = mobileOpenIndex === index;

            return (
              <div key={index}>
                {/* CLICK en item */}
                <button
                  className="flex justify-between items-center w-full py-3 text-lg font-semibold"
                  onClick={() =>
                    hasSubmenu
                      ? setMobileOpenIndex(isOpen ? null : index)
                      : router.push(item.href)
                  }
                >
                  {item.label}
                  {hasSubmenu && <span>{isOpen ? "▲" : "▼"}</span>}
                </button>

                {/* SUBMENU MOBILE */}
                {hasSubmenu && isOpen && (
                  <div className="pl-6 pb-3 space-y-2">
                    {item.submenu!.map((sub, i) => (
                      <a
                        key={i}
                        href={sub.href}
                        className="block py-2 text-gray-700"
                      >
                        {sub.label}
                      </a>
                    ))}
                  </div>
                )}
              </div>
            );
          })}

          {/* USER INFO MOBILE */}
          <div className="mt-4 pt-4 border-t flex items-center gap-3">
            <img
              src={user.avatar || "/assets/user.png"}
              className="h-10 w-10 rounded-full border object-cover"
            />
            <div>
              <p className="font-semibold text-sm">{user.name}</p>
              <p className="text-xs text-gray-500">{user.role}</p>
            </div>
          </div>

          {/* Logout */}
          <button
            onClick={handleLogout}
            className="mt-2 text-sm text-red-600"
          >
            Logout
          </button>
        </div>
      </div>
    </nav>
  );
}
